<template>
    <div id="app">
      <button @click="enableEthereum" class="enableEthereumButton">Enable Ethereum</button>
      <h2>Account: <span class="showAccount">{{ account }}</span></h2>
    </div>
  </template>
  
  <script>
  import detectEthereumProvider from "@metamask/detect-provider";
  
  export default {
    data() {
      return {
        account: null,
      };
    },
    methods: {
      async enableEthereum() {
        const provider = await detectEthereumProvider();

        // Determine the platform
        const userAgent = navigator.userAgent || navigator.vendor || window.opera;
        let platform = "unknown";
        let isMobile = false;

        if (/android/i.test(userAgent)) {
          platform = "Android";
          isMobile = true;
        } else if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
          platform = "iOS";
          isMobile = true;
        } else if (/Win/.test(userAgent)) {
          platform = "Windows";
        } else if (/Mac/.test(userAgent)) {
          platform = "macOS";
        }

        console.log("Platform:", platform);

        if (isMobile) {
          // Redirect to MetaMask mobile app
          window.location.href = "https://metamask.app.link/dapp/ericc55.github.io/ProductTraceability-TMA/";
        } else if (provider && provider === window.ethereum) {
          try {
            const accounts = await window.ethereum.request({
              method: "eth_requestAccounts",
            });
            this.account = accounts[0];
            console.log("Connected Account:", this.account);
          } catch (error) {
            console.error("Error connecting to MetaMask:", error);
          }
        } else {
          alert("Please install MetaMask!");
        }
      },
    },
  };
  </script>
  
  <style scoped>
  .enableEthereumButton {
    /* Add styles as needed */
  }
  </style>
